.field-size-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    z-index: 1;
    background-color: rgba(0,0,0, 0.9);
    transition: 0.3s;
    transform: translateY(-100%);
    padding-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.field-size-wrapper-active {
    transform: translateY(0%);
}

.field-size-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 40%;
    min-width: 300px;
}
.field-size-form p {
    font-size: 1.5rem;
}